.container-fluid {
    height: 100vh;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    align-content: center;
    background-color: white;
    /* background-image: linear-gradient(to top,   #1693b6 0%, #1f72f7 100%); */
}

.container {
    background-color: white;
    width: 20%;
    padding: 50px;
    border: 1px solid rgb(192, 189, 189);
    /* box-shadow: 0 4px 8px rgba(0, 0, 0, 0.5); */
    border-radius: 2px;
}

.header {
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 4px;
    width: 100%;
}

.text {
    color: #005bea;
    font-size: 28px;
    font-weight: 600;
}

.underline {
    width: 61px;
    height: 3px;
    background: #005bea;
    border-radius: 9px;
}

.inputs {
    margin-top: 8%;
    display: flex;
    flex-direction: column;
    gap: 25px;
}

.input {
    position: relative;
    width: 100%;
}

.input .icon {
    position: absolute;
    left: 16px;
    top: 50%;
    transform: translateY(-50%);
}

.input input {
    width: 80%;
    padding: 14px 12px 8px 40px; 
    border: 1px solid rgb(202, 199, 199);
    margin-left: 2%;
    border-radius: 5px;
}
.input input:focus{
    outline-color: #629bf6;
}

.forgot-password {
    margin-top: 20px;
    color: gray;
    font-size: 14px;
}

.forgot-password span {
    color: #005bea;
    cursor: pointer;
}

.submit {
    margin-top: 30px;
    display: flex;
    justify-content: space-around;
}

.btn-submit {
    padding: 10px 12px;
    background-color: #005bea;
    color: white;
    border: none;
    border-radius: 24px;
    cursor: pointer;
    text-align: center;
    width: 100px;
    font-size: 17px;
}

.btn-submit:hover {
    background-color: #004bb5;
}

/* Media Queries for Responsiveness */

@media (max-width: 1200px) {
    .container {
        width: 40%;
        padding: 40px;
    }
}

@media (max-width: 768px) {
    .container {
        width: 60%;
        padding: 30px;
    }

    .text {
        font-size: 24px;
    }

    .input input {
        width: 85%;
    }

    .btn-submit {
        width: 80px;
        font-size: 15px;
    }
}

@media (max-width: 480px) {
    .container {
        width: 80%;
        padding: 20px;
    }

    .text {
        font-size: 20px;
    }

    .input .icon {
        left: 10px;
    }

    .input input {
        width: calc(100% - 40px);
        padding: 12px 8px 8px 36px; 
    }

    .btn-submit {
        width: 70px;
        font-size: 14px;
    }
}
.gray{
    background-color: rgb(202, 199, 199);
    color: rgb(87, 85, 85);
}
.gray:hover {
    background-color: rgb(202, 199, 199);
}
